import React, { useEffect, useState } from 'react'
import weatherPlaces from "../assets/weatherPlaces.json";
import OpenWeatherApi from '../components/OpenWeatherApi';
import TomorrowApi from '../components/TomorrowApi';
import AccWeatherApi from '../components/AccWeatherApi';
import { useTranslation } from 'react-i18next';





const WeatherPage = () => {
  const [selectedApi, setSelectedApi] = useState('');
  const [selectedDistrict, setSelectedDistrict] = useState(null);
  const [selectedTehsil, setSelectedTehsil] = useState(null);
  const [selectedPanchyat, setSelectedPanchyat] = useState(null);
  const [panchyatSelectedItem, setPanchyatSelectedItem] = useState(null);

  const { t } = useTranslation();
  const [tehsilList, setTehsilList] = useState([]);
  const [panchyatList, setPanchyatList] = useState([]);


  const handleAPISelect = (e) => {
    setSelectedApi(e.target.value)
  };


  const handleDistrictSelect = (e) => {
    let items = weatherPlaces.filter((item) => item.DISTRICT === e.target.value);

    items = [...new Set(items.map((item) => item))];
    items.sort();

    setTehsilList(items);
    setSelectedDistrict(e.target.value)

    setSelectedTehsil(null)
    setSelectedPanchyat(null)
    setPanchyatSelectedItem(null)

  };

  const handleTehsilSelect = (e) => {
    setSelectedTehsil(e.target.value)

    let items = weatherPlaces.filter((item) => item.BLOCK === e.target.value);
    items = [...new Set(items.map((item) => item))];
    items.sort();
    setPanchyatList(items);
    setSelectedPanchyat(null)
    setPanchyatSelectedItem(null)
  };


  const handlePanchyatSelect = (e) => {
    setSelectedPanchyat(e.target.value)
    let item = panchyatList.find((item) => item.PANCHAYAT === e.target.value);
    setPanchyatSelectedItem(item);
  };




  return (
    <div className='dasboard_page_container'>

      <div className='main_dashboard'>
        <div className='left_panel'>
          <div className="left_panel_container">
            <div className='border-secondary p-2 mb-2'>

              <label className=''>{t('data.select_district')}</label>
              <select className="form-select mb-3"
                value={selectedDistrict ? selectedDistrict : ''}
                onChange={handleDistrictSelect}>
                <option value="">{t('data.select_placeholder')}</option>
                {[...new Set(weatherPlaces.map((item) => item.DISTRICT))].map((item, index) =>
                  <option key={index} value={item}>{t(`location.${item}`)}</option>
                )}
              </select>



              <label className='mt-3'>{t('data.select_block')}</label>
              <select className="form-select mb-2"
                value={selectedTehsil ? selectedTehsil : ''}
                disabled={tehsilList.length === 0}
                onChange={handleTehsilSelect} >
                <option value="">{t('data.select_placeholder')}</option>
                {[...new Set(tehsilList.map((item) => item.BLOCK))].map((item, index) =>
                  <option key={index} value={item}>{t(`location.${item}`)}</option>
                )}
              </select>

              <label className='mt-3'>{t('data.select_panchayat')}</label>
              <select className="form-select mb-2"
                value={selectedPanchyat ? selectedPanchyat : ''}
                disabled={panchyatList.length === 0}
                onChange={handlePanchyatSelect} >
                <option value="">{t('data.select_placeholder')}</option>
                {[...new Set(panchyatList.map((item) => item.PANCHAYAT))].map((item, index) =>
                  <option key={index} value={item}>{t(`location.${item}`)}</option>
                )}
              </select>


              <label className='mt-3'>{t('data.select_api')}</label>
              <select className="form-select mb-2"
                value={selectedApi}
                onChange={handleAPISelect} >
                <option value="">{t('data.select_placeholder')}</option>
                <option value="tomorrow">Tomorrow.io API</option>
                <option value="accuweather">Accuweather API</option>
                <option value="open_weather">Open Weather API</option>


              </select>



            </div>

          </div>
        </div>




        <div className='right_panel' >

          {panchyatSelectedItem ? (
            <>
              {selectedApi === "accuweather" ? (
                <AccWeatherApi panchyatSelectedItem={panchyatSelectedItem} />
              ) : selectedApi === "open_weather" ? (
                <OpenWeatherApi panchyatSelectedItem={panchyatSelectedItem} />
              ) : selectedApi === "tomorrow" ? (
                <TomorrowApi panchyatSelectedItem={panchyatSelectedItem} />
              ) :
                <div className='right_panel_container my-4'>
                  {t('data.select_api_placeholder')}
                </div>
              }

            </>
          ) : (
            <div className='right_panel_container my-4'>
              {t('data.select_location_placeholder')}            
            </div>)}




        </div>




      </div>

    </div>
  )
}

export default WeatherPage