import React, { useEffect, useState } from 'react'
// import { currentWeatherData } from "../assets/weatherapiData/openWeather"
// import { forecastData } from "../assets/weatherapiData/openWeather"


import HumidityIcon from "../assets/weather-icons/humidity.svg";
import TempIcon from "../assets/weather-icons/thermometer-1-2.svg";
import MinTempIcon from "../assets/weather-icons/thermometer-0.svg";
import MaxTempIcon from "../assets/weather-icons/thermometer-full.svg";
import WindPressureIcon from "../assets/weather-icons/wind-pressure.png";
import VisibilityIcon from "../assets/weather-icons/visibility.png";
import FeelsLikeIcon from "../assets/weather-icons/feels-like.png";
import LocationIcon from "../assets/weather-icons/location.png";
import WindIcon from "../assets/weather-icons/wind.svg";
import SunriseIcon from "../assets/weather-icons/sunrise.svg";
import SunsetIcon from "../assets/weather-icons/sunset.svg";
import {  unixTimestampToTime } from '../helpers/functions';
import { useAlertContext } from '../contexts/AlertContext';
import axios from 'axios';
import { useTranslation } from 'react-i18next';


const OpenWeatherApi = ({ panchyatSelectedItem }) => {

  const [forecastData, setForecastData] = useState(null)
  const [currentWeatherData, setCurrentWeatherData] = useState(null)
  const { setAlertMessage, setShowAlert } = useAlertContext();
  const { t } = useTranslation();

  const fetchCurrentWeatherData = async (selectedItem) => {
    if (selectedItem && selectedItem.LAT) {

      const lat = selectedItem.LAT;
      const lon = selectedItem.LONG;
      const apiKey = process.env.REACT_APP_OPENWEATHER_API_KEY;
      try {
        const apiUrl = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&units=metric&appid=${apiKey}`;
        const response = await axios.get(apiUrl);

        if (response.data) {
          setCurrentWeatherData(response.data);

        } else {
          setCurrentWeatherData(null)
        }
      } catch (error) {
        setAlertMessage("Error fetching data:", error);
        setShowAlert(true);
      }
    }
  };

    const fetchForecastWeatherData = async (selectedItem) => {
    if (selectedItem && selectedItem.LAT) {

      const lat = selectedItem.LAT;
      const lon = selectedItem.LONG;
      const apiKey = process.env.REACT_APP_OPENWEATHER_API_KEY;
      try {
        const apiUrl = `https://api.openweathermap.org/data/2.5/forecast?lat=${lat}&lon=${lon}&units=metric&appid=${apiKey}`;
        const response = await axios.get(apiUrl);

        if (response.data) {
          setForecastData(response.data);

        } else {
          setForecastData(null)
        }
      } catch (error) {
        setAlertMessage("Error fetching data:", error);
        setShowAlert(true);
      }
    }
  };



  useEffect(() => {
    if (panchyatSelectedItem && panchyatSelectedItem.LAT) {
      fetchCurrentWeatherData(panchyatSelectedItem);
      fetchForecastWeatherData(panchyatSelectedItem);

    }
  }, [panchyatSelectedItem, panchyatSelectedItem?.LAT]);






  function formatForecastDateTime(timestamp, timezone) {
    const date = new Date((timestamp + timezone) * 1000); // Adjust timestamp by adding the timezone offset
    const options = { day: 'numeric', month: 'long', hour: '2-digit', minute: '2-digit', timeZone: 'UTC' };
    return date.toLocaleString('en-GB', options).replace(' at', ',');
}



  return (
    <div>
      <div className='card_heading'>
        <h2>{t("AccWeather.currentWeather")}</h2>
      </div>

      {currentWeatherData && currentWeatherData.main ? (
        <>
          <div className='row'>
            <div className='col-md-6 col-sm-12 col-lg-6'>
              <div className='weather_main_card'>
                <div className='weather_card'>
                  <img
                    src={`https://openweathermap.org/img/wn/${currentWeatherData.weather[0].icon}.png`}
                    alt='weather_icon'
                  />
                  <div className='weather_content'>
                    <p className='weather_card_text'>
                      {currentWeatherData.weather[0].main},{" "}
                      {currentWeatherData.weather[0].description}
                    </p>
                    <p className='weather_card_value'>
                      {formatForecastDateTime(currentWeatherData.dt, currentWeatherData.timezone)}
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={LocationIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.location")}</p>
                    <p className='weather_card_value'>
                      {panchyatSelectedItem.PANCHAYAT}, {panchyatSelectedItem.DISTRICT}
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={TempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.temperature")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.temp} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={FeelsLikeIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.feelsLike")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.feels_like} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={MinTempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.minTemp")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.temp_min} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={MaxTempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.maxTemp")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.temp_max} °C</p>
                  </div>
                </div>
              </div>
            </div>

            <div className='col-md-6 col-sm-12 col-lg-6'>
              <div className='weather_main_card'>
                <div className='weather_card'>
                  <img src={SunriseIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.sunrise")}</p>
                    <p className='weather_card_value'>
                      {unixTimestampToTime(currentWeatherData.sys.sunrise)}
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={SunsetIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.sunset")}</p>
                    <p className='weather_card_value'>
                      {unixTimestampToTime(currentWeatherData.sys.sunset)}
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={WindPressureIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.pressure")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.pressure} hPa</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={HumidityIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.humidity")}</p>
                    <p className='weather_card_value'>{currentWeatherData.main.humidity} %</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={WindIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.windSpeed")}</p>
                    <p className='weather_card_value'>
                      {(currentWeatherData.wind.speed * 3.6).toFixed(2)} km/h
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={VisibilityIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className='weather_card_text'>{t("AccWeather.visibility")}</p>
                    <p className='weather_card_value'>
                      {(currentWeatherData.visibility / 1000).toFixed(2)} km
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>

      ) : (
        <div className='right_panel_container my-4'>
          {t('data.select_location_placeholder')}
        </div>

      )}





<div className='card_heading'>
        <h2>{t('AccWeather.3hourForecast')}</h2>
      </div>

      {forecastData && forecastData.list.length > 0 ? (
        <div className='forecast_container'>
          {forecastData.list.map((item, index) => (
            <div className='col-md-6 col-sm-12 col-lg-6' key={index}>
              <div className='forecast_cards_details column_3'>

                <div className='weather_card'>
                  <img src={`https://openweathermap.org/img/wn/${item.weather[0].icon}.png`} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">
                      {item.weather[0].main}, {item.weather[0].description}
                    </p>
                    <p className='weather_card_value'>
                      {formatForecastDateTime(item.dt, forecastData.city.timezone)}
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={TempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.temperature')}</p>
                    <p className='weather_card_value'>{item.main.temp} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={WindPressureIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.pressure')}</p>
                    <p className='weather_card_value'>{item.main.pressure} hPa</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={HumidityIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.humidity')}</p>
                    <p className='weather_card_value'>{item.main.humidity} %</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={MinTempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.minTemp')}</p>
                    <p className='weather_card_value'>{item.main.temp_min} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={FeelsLikeIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.feelsLike')}</p>
                    <p className='weather_card_value'>{item.main.feels_like} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={VisibilityIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.visibility')}</p>
                    <p className='weather_card_value'>
                      {(item.visibility / 1000).toFixed(2)} km
                    </p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={MaxTempIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.maxTemp')}</p>
                    <p className='weather_card_value'>{item.main.temp_max} °C</p>
                  </div>
                </div>

                <div className='weather_card'>
                  <img src={WindIcon} alt='weather_icon' />
                  <div className='weather_content'>
                    <p className="weather_card_text">{t('AccWeather.windSpeed')}</p>
                    <p className='weather_card_value'>
                      {(item.wind.speed * 3.6).toFixed(2)} km/h
                    </p>
                  </div>
                </div>

              </div>
            </div>
          ))}
        </div>


      ) : (
        <div className='right_panel_container my-4'>
          {t('data.select_location_placeholder')}
        </div>

      )}


    </div>
  )
}

export default OpenWeatherApi